<template>
    <el-dialog :title="title" :visible.sync="dialogVisible" width="30%" :before-close="hideDialog">
        <div class="add">
            <el-form ref="form" :model="rowData" label-width="80px" :rules="rules">
                <el-form-item label="姓名" prop="userName">
                    <el-input v-model="rowData.userName"></el-input>
                </el-form-item>
                <el-form-item label="登录账号" prop="loginName">
                    <el-input v-model="rowData.loginName" v-if="rowData.id" disabled></el-input>
                    <el-input v-model="rowData.loginName" v-else></el-input>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input v-model="rowData.password" type="password"></el-input>
                </el-form-item>
                <el-form-item label="确认密码" prop="rePassword">
                    <el-input v-model="rowData.rePassword" type="password"></el-input>
                </el-form-item>
                <el-form-item label="电话" prop="userMobile">
                    <el-input v-model="rowData.userMobile" placeholder="请输入手机号"></el-input>
                </el-form-item>
                <el-form-item label="性别">
                    <el-select v-model="rowData.userSex" placeholder="请选择性别" clearable>
                        <!-- v-for引用sexEnum不用冒号 -->
                        <el-option v-for="(value,key) in sexEnum" :key="key" :label="value" :value="value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="门店">
                    <el-select v-model="rowData.deptNo" placeholder="请选择门店">
                        <el-option label="采购部" value="1"></el-option>
                        <el-option label="库管部" value="2"></el-option>
                        <el-option label="物资部" value="3"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
        </div>
            <div slot="footer" class="footer">
                <el-button size="mini" @click="$emit('hideDialog')">取消</el-button>
                <el-button type="primary" size="mini" @click="submit('form')">确定</el-button>
            </div>
    </el-dialog>
</template>

<script>
export default {
  name: 'addUser',
  props: {
    rowData: {
      type: Object,
      default: function () {
        return {}
      }
    },
    // Map对象
    sexEnum: {}
  },
  data () {
    let checkPsw = (rule, value, callback) => {
      if (value !== this.rowData.password) {
        return callback(new Error('2次输入密码不一致'))
      }
      callback()
    }
    let checkMobile = (rule, value, callback) => {
      let reg = /^1[3456789]\d{9}$/
      if (!reg.test(value)) {
        console.warn('手机号格式不正确')
        return callback(new Error('手机号格式不正确'))
      }
      callback()
    }
    return {
      dialogVisible: true,
      formData: {
        id: '',
        userName: '',
        loginName: '',
        password: '',
        rePassword: '',
        userMobile: '',
        userSex: '',
        deptNo: ''
      },
      title: '添加人员',
      rules: {
        userName: [{required: true, message: '请输入姓名', trigger: 'blur'}],
        loginName: [{required: true, message: '请输入登录名（英文或数字）', trigger: 'blur'}],
        rePassword: [{validator: checkPsw, trigger: 'blur'}],
        userMobile: [{required: true, message: '请输入手机号', trigger: 'blur'}, {validator: checkMobile, trigger: 'blur'}]
      }
    }
  },
  created () {
    this.formData = Object.assign({}, this.formData, this.rowData)
    if (this.formData.id) {
      this.title = '修改人员'
    }
  },
  methods: {
    hideDialog () {
      this.$emit('hideDialog')
    },
    submit (form) {
      this.$refs[form].validate(valid => {
        if (valid) {
          console.info('表单检验通过')
          alert('表单检验通过')
          this.$emit('hideDialog')
        } else {
          console.warn('表单检验不通过')
        }
      })
    }
  }
}
</script>

<style scoped>
    .add {
        margin-right: 40px;
        /*margin-top: 30px;*/
        margin-left: 10px;
    }
    .footer{
        align-items:center;
    }
</style>



