<template>
  <div>
    <div class="top">
      <el-select v-model="value" filterable placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.label"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
      <span class="demonstration">默认</span>
      <el-date-picker
        v-model="daterange"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      >
      </el-date-picker>
    </div>
    <div class="middle">
      <el-button type="primary" @click="setAddDialog()">打开Dialog</el-button>
      <open-dialog
        v-if="showAddDialog"
        :rowData="rowData"
        :sexEnum="sexEnum"
        @hideDialog="showAddDialog = false"
      ></open-dialog>
    </div>
  </div>
</template>

<script>
import OpenDialog from './OpenDialog'

export default {
  name: 'DialogPage',
  data () {
    return {
      userList: [],
      currentPage: 1, // 初始页
      pagesize: 10,
      total: 0,
      showAddDialog: false,
      rowData: {
        id: 1,
        userName: 'zengwqiang',
        loginName: 'wzeng'
      },
      sexEnum: {
        1: '男',
        2: '女'
      },
      options: [
        {
          value: '选项1',
          label: '黄金糕'
        },
        {
          value: '选项2',
          label: '双皮奶'
        },
        {
          value: '选项3',
          label: '蚵仔煎'
        },
        {
          value: '选项4',
          label: '龙须面'
        },
        {
          value: '选项5',
          label: '北京烤鸭'
        }
      ],
      value: '',
      daterange: ''
    }
  },
  components: {
    OpenDialog
  },
  methods: {
    setAddDialog () {
      this.showAddDialog = true
    }
  }
}
</script>

<style scoped>
a {
  color: #42b983;
}
.top{
  float: left;
}
.middle {
  margin-right: 10px;
  margin-top: 10px;
}
</style>



